    <Button @btn-click="$emit('btn-click')" :text="showAddTask ? 'Close' : 'Add Task'"
            :color="showAddTask ? '#127779' : 'green'" />
  </header>
</template>

<script>
import Button from "./Button";
export default {
  name: "Header",
  components: {
    Button
  },
  props: {
    title: String,
    showAddTask: Boolean,
  }
}
</script>

<style scoped>
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
